.c-callout {
  position: relative;
  padding: 0 1rem;
  margin: 1rem 0;
  border-left: 4px solid #bcbdc2;
  border-radius: 0.25rem;
}

.c-callout .c-chart-wrapper {
  position: absolute;
  top: 10px;
  left: 50%;
  float: right;
  width: 50%;
}

.c-callout-bordered {
  border: 1px solid #bcbdc2;
  border-left-width: 4px;
}

.c-callout code {
  border-radius: 0.25rem;
}

.c-callout h4 {
  margin-top: 0;
  margin-bottom: .25rem;
}

.c-callout p:last-child {
  margin-bottom: 0;
}

.c-callout + .c-callout {
  margin-top: -0.25rem;
}

.c-callout-primary {
  border-left-color: #321fdb;
